import PropTypes from 'prop-types';

function FriendsList({ friends }) {
  return (
    <ul class="friend-list">
      {friends.map(friend => (
        <li class="item" key={friend.id}>
          <span
            className={(friend.isOnline = true
                ? 'status is-online'
                : 'status is-offline'
            )}></span>
          <img class="avatar" src={friend.avatar} alt={friend.name} width="48" />
          <p class="name">{friend.name}</p>
        </li>
      ))}
    </ul>
  );
}

FriendsList.propTypes = {
  id: PropTypes.arrayOf(PropTypes.string.isRequired),
  avatar: PropTypes.arrayOf(PropTypes.string.isRequired),
  name: PropTypes.arrayOf(PropTypes.string.isRequired),
  isOnline: PropTypes.arrayOf(PropTypes.bool.isRequired),
};

export default FriendsList;
